<template>
    <main>
        <div class="weui-cells weui-cells_form quit">
            <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="abcde">
                <div class="weui-cell__hd" style="width:1.7rem;">
                    退房订单号
                </div>
                <div class="weui-cell__bd">
                    <p v-if="!oddNumbers">请选择订单号</p>
                    {{oddNumbers}}
                </div>
            </div>
            <div class="weui-cell">
                <div class="weui-cell__hd">
                    <label class="weui-label">退房房号</label>
                </div>
                <div class="weui-cell__bd">
                    1008
                </div>
            </div>
            <div class="weui-cell weui-cell_switch">
                <div class="weui-cell__bd">
                    <div class="Prompt">需开发票</div>
                </div>
                <div class="weui-cell__ft">
                    <label for="switchCP1" class="weui-switch-cp">
                        <input id="switchCP1" v-model="abcef" class="weui-switch-cp__input" type="checkbox">
                        <div class="weui-switch-cp__box"></div>
                    </label>
                </div>
            </div>
            <div class="weui-cell">
                <div class="weui-cell__hd">
                    <label class="weui-label">发票抬头</label>
                </div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="text" v-model="name" placeholder="请填写发票抬头">
                </div>
            </div>
            <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="abcd">
                <div class="weui-cell__hd" style="width:1.7rem;">
                    发票类目
                </div>
                <div class="weui-cell__bd">
                    <p v-if="!invoice">请选择发票类目</p>
                    {{invoice}}
                </div>
            </div>
            <div class="btn">
                <a href="javascript:;" class="weui-btn weui-btn_primary" :class="{'weui-btn_disabled':!abc||!name}">确定</a>
            </div>
            <Success v-if="isok" text="已预约"></Success>
        </div>
        <LOGO></LOGO>
    </main>
</template>

<script type='text/ecmascript-6'>
import LOGO from "@/components/Logo"
import Success from "@/components/Success"
import weui from "@/assets/js/weui.min";
export default {
    data() {
        return {
            abc: '',
            name: '',
            isok: false,
            invoice: '',
            oddNumbers: "",
            abcef: true
        }
    },
    components: {
        LOGO,
        Success
    },
    methods: {
        abcd() {
            var _this = this;
            weui.picker([{
                label: '会员卡支付',
                value: 0
            }, {
                label: '到店支付',
                value: 1
            }, {
                label: '在线支付',
                value: 2
            }], {
                    onChange: function(result) {
                        // console.log(result, '1111');
                    },
                    onConfirm: function(result) {
                        _this.invoice = result[0].label
                    }
                });
        },
        abcde() {
            var _this = this;
            weui.picker([{
                label: '1D049490980890',
                value: 0
            }, {
                label: '2D049490980890',
                value: 1
            }, {
                label: '3D049490980890',
                value: 2
            }], {
                    onChange: function(result) {
                        // console.log(result, '1111');
                    },
                    onConfirm: function(result) {
                        _this.oddNumbers = result[0].label
                    }
                });
        }

    }
}
</script>

<style scoped lang='less' rel='stylesheet/less'>
main {
    display: flex;
    flex-flow: column;
    min-height: 100vh;
    padding-bottom: 1.3rem;
}

.quit {
    flex: 1;
    background: #f4f4f4;
}
</style>



